@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
	box-sizing: border-box;
}

html {
	font-family: 'Noto Sans JP', sans-serif;
}

body {
	margin: 0;
	background-color: #3f3f3f;
}

.basicBack {
	position: absolute;
	width: 95%;
	height: auto;
	left: 2%;
	top: 5%;
	background-color: rgba(80, 80, 80, 0.719);
}

.basicBackFlexCover {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	margin-top: 50px;
	margin-bottom: 70px;
	width: 98%;
	height: 98%;
	left: 1%;
	top: 1%;
}

@media only screen and (min-width: 600px) {
	.basicBack {
		width: 70%;
		left: 15%;
		top: 15%;
	}
	.basicBackFlexCover {
		width: 90%;
		height: 90%;
		top: 5%;
	}
}

.itemContainer {
	position: relative;
	margin: 20px;
	width: 80%;
	height: 300px;
	left: 5%;
	top: 10%;
	background-color: rgba(156, 156, 156, 0.295);
}
.itemView {
	position: relative;
	width: 90%;
	height: 55%;
	top: 10%;
	left: 5%;
}
.itemShowImage {
	position: relative;
	width: 60%;
	height: 60%;
	left: 20%;
	top: 20%;
	border-style: dashed;
	border-color: gray;
	border-radius: 0.2em;
}
.itemHeader {
	position: relative;
	width: 90%;
	height: 35%;
	left: 5%;
	font-family: Arial, Helvetica, sans-serif;
	font-size: x-large;
	text-align: center;
	padding-top: 15%;
}

@media only screen and (min-width: 600px) {
	.itemContainer {
		margin: 20px;
		width: 20%;
		height: 300px;
		top: 20%;
	}
	.itemView {
		width: 90%;
		height: 55%;
		top: 10%;
		left: 5%;
	}
	.itemShowImage {
		width: 60%;
		height: 60%;
		left: 20%;
		top: 20%;
	}
	.itemHeader {
		width: 90%;
		height: 35%;
		left: 5%;
	}
}

.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
}

